import styles from "./Form.module.css";
import {nanoid} from "nanoid";
import { useState, memo } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { addContact } from '../../redux/contacts/contactsOperations';
import { getVisibleContacts} from '../../redux/contacts/contactsSelector'

function Form() {
  const [name, setName] = useState('');
  const [number, setNumber] = useState('');
  const dispatch = useDispatch();
  const contacts = useSelector(getVisibleContacts);
  
  const nameRandomId = nanoid();
  const numberRandomId = nanoid();

 const handleSubmit = (e) => {
    e.preventDefault();
    if (contacts.some((contact) => contact.name.toLowerCase() === name.toLowerCase())) {
      return alert(`${name} is already in contacts`);
    }
   dispatch(addContact({ name, number }));
   setName('');
   setNumber('');
  };

   const handleChange = ({ target }) => {
    const { name, value } = target;
    switch (name) {
        case 'name':
          setName(value);
          return;
        case 'number':
          setNumber(value);
          return;
        default:
          return;
      }
  };
  
    return (
      <div>
        <form onSubmit={handleSubmit} className={styles.addForm}>
          <label className={styles.label} htmlFor={nameRandomId}>
            Name:
            <input
              onChange={handleChange}
              value={name}
              id={nameRandomId}
              className={styles.input}
              type="text"
              name="name"
              pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
              title="     , ,   .  Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan  . ."
              required
            />
          </label>

          <label htmlFor={numberRandomId} className={styles.label}>
            Number:
            <input
              className={styles.input}
              value={number}
              id={numberRandomId}
              onChange={handleChange}
              name="number"
              type="tel"
              pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
              title="        , ,       +"
              required
            />
          </label>
          <button type="submit" className={styles.btn}>
            Add Contact
          </button>
        </form>
      </div>
    );
  }

export default memo(Form);
